<template>
  <div class="container">
    <div class="form">
      <el-form label-width="180px" :inline="true" :model="fleetDeclare" ref="fleetDeclareForm">
        <el-form-item label="归属机构:" prop="ownOrg">
          <el-select placeholder="请选择机构" v-model="fleetDeclare.orgCode">
            <el-option
              v-for="org in orgList"
              :key="org.orgCode"
              :label="org.orgName"
              :value="org.orgCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="归属渠道:" prop="channel">
          <el-select placeholder="请选择渠道" v-model="fleetDeclare.channel">
            <el-option
              v-for="channel in channelList"
              :key="channel.channelCode"
              :label="channel.channelName"
              :value="channel.channelCode">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="归属业务员:" prop="ownSalesMan">
          <el-input v-model="fleetDeclare.ownSalesMan"></el-input>
        </el-form-item>
        <el-form-item label="投保人名称:" prop="policyHolder">
          <el-input v-model="fleetDeclare.policyHolder"></el-input>
        </el-form-item>
        <el-form-item label="续保状态:" prop="insuranceType">
          <el-select v-model="fleetDeclare.insuranceType">
            <el-option value="1" label="新保"></el-option>
            <el-option value="2" label="续保"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="核保人预估赔付率(%)" prop="expPayRate">
          <el-input v-model="fleetDeclare.expPayRate"></el-input>
        </el-form-item>
      </el-form>
    </div>
    <div class="sector-list">
      <div class="title">车队类型</div>
      <div class="biz-sector">
        <el-form label-width="120px" :inline="true">
          <el-form-item>
            <el-checkbox-group v-model="fleetTypeCode">
              <el-checkbox
                v-for="item in fleetTypeList"
                :key="item.code"
                :label="item.code"
                name="fleetType">
                {{item.name}}
              </el-checkbox>
              <el-checkbox label="0" style="color: red"> 未勾选板块出单取0费用</el-checkbox>
            </el-checkbox-group>
          </el-form-item>
        </el-form>
      </div>
    </div>
    <div class="option">
      <el-button @click="importDialogVisible = true">导&nbsp;&nbsp;入</el-button>
      <el-button>配置部门</el-button>
      <back-button type="next" @next="riskInfo">下一步</back-button>
    </div>
    <base-table
      :columns="importCols"
      :data="importData"
      :show-header="true"
      v-show="importTableVisible"
      >
    </base-table>

    <el-dialog title="信息导入" :visible.sync="importDialogVisible" width="400px">
      <el-upload
        action="https://jsonplaceholder.typicode.com/posts/"
        :auto-upload="false"
        drag
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
        <div class="el-upload__tip" slot="tip">Excel格式，以.xls为后缀名</div>
      </el-upload>
      <div slot="footer" style="text-align: center" class="dialog-footer">
        <el-button type="primary" plain @click="importDialogVisible = false ; importTableVisible = true">导入</el-button>
        <el-button type="primary" plain @click="importDialogVisible = false">关闭</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>

const BackButton = () => import('@/components/BackButton')
const BaseTable = () => import('@/components/BaseTable')
export default {
  name: 'baseInfo',
  components: {
    BackButton,
    BaseTable
  },
  data () {
    return {
      fleetDeclare: {
        ownOrg: '',
        channel: '',
        ownSalesMan: '',
        policyHolder: '',
        expPayRate: ''
      },
      fleetTypeCode: [],
      orgList: [
        {orgCode: '000000', orgName: '北京分公司'},
        {orgCode: '000001', orgName: '天津分公司'},
        {orgCode: '000002', orgName: '上海分公司'}
      ],
      channelList: [
        {channelCode: '001', channelName: '保险公司车险'},
        {channelCode: '002', channelName: '银行邮政代理'},
        {channelCode: '003', channelName: '电话车险'},
        {channelCode: '004', channelName: '网销车险'},
        {channelCode: '005', channelName: '4S店保险中心车险'},
        {channelCode: '006', channelName: '维修店专业车险'}
      ],
      fleetTypeList: [
        {code: '1', name: '企业未营业车'},
        {code: '2', name: '营业公路客运'},
        {code: '3', name: '营业城市公交'},
        {code: '4', name: '营业旅游车'},
        {code: '5', name: '非营业货车(含特四)'},
        {code: '6', name: '营业货车(含特四)'},
        {code: '7', name: '特一'},
        {code: '8', name: '特二'},
        {code: '9', name: '特三'},
        {code: '10', name: '营业出租租赁客车'}
      ],
      importCols: [
        {prop: 'd1', label: '车队类型', aligin: 'center'},
        {prop: 'd2', label: '座位数/吨位数/特种车类型', aligin: 'center'},
        {prop: 'd3', label: '车龄分段', aligin: 'center'},
        {prop: 'd4', label: '承保车辆数', aligin: 'center'},
        {prop: 'd5', label: '平均新车购置价(万元)', aligin: 'center'},
        {prop: 'd6', label: '商业险自主渠道系数(0.85-1.15)', aligin: 'center'},
        {prop: 'd7', label: '商业险自主核保系数(0.85-1.15)', aligin: 'center'},
        {prop: 'd8', label: '交强险', aligin: 'center'},
        {prop: 'd9', label: '车损', aligin: 'center'},
        {prop: 'd10', label: '三者保额(万元)', aligin: 'center'},
        {prop: 'd11', label: '盗抢', aligin: 'center'},
        {prop: 'd12', label: '车上人员保额(驾驶员，万元)', aligin: 'center'},
        {prop: 'd13', label: '车上人员总保额(乘客，万元)', aligin: 'center'},
        {prop: 'd14', label: '车上货物保额(万元)', aligin: 'center'},
        {prop: 'd15', label: '玻璃类型', aligin: 'center'},
        {prop: 'd16', label: '自燃', aligin: 'center'},
        {prop: 'd17', label: '其他险别', aligin: 'center'},
        {prop: 'd18', label: '备注', aligin: 'center'}
      ],
      importData: [
        {
          d1: '出租、租赁营业客车06',
          d2: '6座以下',
          d3: '[0-1)年',
          d4: '300',
          d5: '20',
          d6: '1.12',
          d7: '1.12',
          d8: '是',
          d9: '是',
          d10: '30',
          d11: '是',
          d12: '2',
          d13: '2',
          d14: '2',
          d15: '国产',
          d16: '是',
          d17: '是',
          d18: '无'
        }
      ],
      importDialogVisible: false,
      importTableVisible: false
    }
  },
  methods: {
    riskInfo () {
      this.$router.push('/admin/fleet/declare/modify/riskInfo')
    }
  }
}
</script>

<style scoped>
  .sector-list {
    background-color: #F8F7FF;
    border-left:#795CF9 3px solid;
    box-shadow: #DBD5F8 0px -2px 2px;
  }

  .sector-list > .title {
    padding-left: 16px;
    height: 50px;
    line-height: 50px;
    font-size: 15px;
    border-bottom: solid 1px #eee;
  }

  .sector-list > .biz-sector {
    padding: 12px;
  }

  .el-checkbox {
    margin: 5px 5px 5px 5px;
  }
</style>



